  .navigation-container {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
  }
  .navigation-header {
    display: flex;
    height: 55px;
    padding: 0 30px 0 30px;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(to right, var(--background-color) 50%, var(--accent-color2) 70%, var(--accent-color) 100%);
  }

  .navigation {
    display: flex;
    height: 35px;
    padding: 0 20px 0 20px;
    align-items: center;
    background-color: var(--primary-color);
    overflow: hidden;
  }

  .side-section-left {
    display: flex;
    width: 200px;
    align-items: center;
    justify-content: start;
  }
  .side-section-right {
    display: flex;
    width: 200px;
    align-items: center;
    justify-content: end;
  }

  .side-section-right p {
    font-size: 18px;
    margin-right: 10px;
    font-weight: 600;
  }

  .logout-btn {
    border: solid 0;
    padding: 8px 15px;
    border-radius: 10px;
    background-color: var(--accent-color3);
    font-weight: 600;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }

  .logout-btn:hover {
    color: var(--text-light);
    background-color: #00aca4;

  }

  .nav-btn {
    display: flex;
    align-items: center;
    padding: 0 30px 0 30px;
    height: 100%;
  }

  .nav-link {
    text-decoration: none;
    font-weight: 600;
    color: var(--text-dark);
    cursor: pointer;
  }

  .nav-link-act {
    color: var(--text-light);
  }

  .nav-link:hover {
    color: var(--text-light);
  }

  .active-btn{
    position: relative;
  }

  .active-btn::after {
  content: '';
  position: absolute;
  display: flex;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px; 
  background-color: var(--accent-color3);
   border-top-left-radius: 5px; 
  border-top-right-radius: 5px;
  box-shadow: 0 0 8px var(--accent-color3);
}