.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.form-box {
  background: var(--primary-color);
  border-radius: 15px;
  padding: 20px 20px 40px 20px;
  width: 100%;
  max-width: 400px;
  text-align: center;
}
.login-btn{
  background: var(--accent-color);
  opacity: 0.8;
  color: var(--text-dark);
  font-size: 16px;
  border: none;
  border-radius: 4px;
  padding: 15px 80px 15px 80px;
  box-shadow: 0 7px 15px rgba(0, 0, 0, 0.619);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.login-btn:hover{
  opacity: 1;
}

.login-icons{
  font-size: 25px;
  color: var(--text-dark);
  position: absolute;
  top: 12px;
  left: 30px;
}