.materials-container{
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 90px;
  left: 0;
  bottom: 0;
  width: 400px;
  background-color: var(--primary-color);
  z-index: 100;
  overflow: auto;
}
.material-btn{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  padding: 0px 20px;
  border-radius: 10px;
  border-bottom: solid 1px var(--accent-color3);
  transition: all 0.3s ease-out;
}

.material-btn:hover{
  background-color: var(--secondary-color);
}

.material-btn h2{
  text-align: left;
  margin: 10px;
  max-width: 300px;
  cursor: "pointer";
}