.studio-container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 100px;

}
.studio-grid{
  display: grid;
  margin: 20px;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}
.room-btn{
  display: flex;
  width: 450px;
  height: 150px;
  border-radius: 20px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.room-btn:hover{
  transform: scale(0.95);
  opacity: 0.95;
}
.room-btn:hover img {
  transform: scale(1.1);
}
.room-btn div{
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--accent-color3);
  font-size: 34px;
  background-color: var(--primary-color);
  width: 100%;
  height: 100%;
  text-align: center;
  
}

.room-btn img{
  height: 150px;
  width: 150px;
  background-color: var(--secondary-color);
  padding: 10px;
    transition: all 0.3s ease-in-out;

}
.map {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 75vh;
  overflow-x: auto;
  border-radius: 20px;
  background-color: var(--primary-color);
}
.table-map{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;
  border-radius: 20px 0 0 20px;
  background-color: var(--primary-color);
}

.tech-info-container {
  display: fixed;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  background-color: var(--secondary-color);
  border-radius: 0 20px 20px 0;
  padding: 20px;
  width: 500px;
  right: 0;
}

.tech-info-container h1 {
  color: var(--accent-color3);
  margin-bottom: 30px;
  font-size: 35px;
  text-align: center;
}

.tech-info-container p {
  color: var(--text-light);
  margin-bottom: 5px;
  font-size: 22px;
  text-align: center;
}

/* EDIT */
.add-container{
  position: sticky;
  width: 350px;
  top: 100px;
  align-items: center;
  flex-direction: column;
  background-color: var(--primary-color);
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  height: 100%;
}

.add-table-container{
  display: flex;
  flex-direction: column;
  position: absolute;
  align-items: center;
  padding: 50px;
  bottom: 40px;
  right: 40px;
  background-color: var(--secondary-color);
  width: 0px;
  height: 0px;
  opacity: 0;
  border-radius: 20px;
  overflow: hidden;
  transition: opacity 0.3s ease-in-out, width 0.8s ease-in-out , height 0.8s ease-in-out;
  z-index: 100;
}

.add-table-container-open{
  display: flex;
  width: 280px;
  height: 300px;
  opacity: 1;
  
}

.add-table-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 30px;
  right: 30px;
  font-size: 40px;
  height: 60px;
  width: 60px;
  border-radius: 30px;
  background-color: var(--accent-color3);
  color: var(--text-light);
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  z-index: 100;
}
.add-table-btn-open{
  transform: rotate(45deg);

}
.add-table-btn:hover{
  transform: rotate(180deg);
}
.cancel-edit-btn{
  background-color: rgb(248, 52, 52);
  padding: 5px 20px;
  border-radius: 10px;
  font-size: 20px;
  margin-left: 40px;
  color: var(--text-light);
  transition: all 0.3s ease-in-out;
}
.cancel-edit-btn:hover{
  scale: 1.1
}

.save-edit-btn{
  background-color: var(--accent-color);
  padding: 5px 20px;
  border-radius: 10px;
  font-size: 20px;
  margin-left: 20px;
  color: var(--text-light);
  transition: all 0.3s ease-in-out;

}
.save-edit-btn:hover{
  scale: 1.1
}

