@import './_login.css';
@import './_nav.css';
@import './_studio.css';
@import './_management.css';
@import './_materials.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Remove default browser number input arrows */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"]::before {
  content: "↑↓";
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  font-size: 18px;
  color: #888;
}


/* For WebKit Browsers (Chrome, Safari) */
::-webkit-scrollbar {
  width: 8px;  
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--primary-color); /* Track background */
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--text-dark); /* Scrollbar color */
  border-radius: 4px;
}

/* Remove arrows */
::-webkit-scrollbar-button {
  display: none;
}
::-webkit-scrollbar-corner{
  display: none;
}


p{
  margin: 0;
}

td{
  padding: 10px;
  text-align: center;
  color: var(--text-light);
  border-bottom: solid 1px black; 
}

:root{
  --background-color: #1c212b;
  --primary-color: #292f3b;
  --secondary-color: #3a414d;
  --accent-color: #0357cb;
  --accent-color2: #022562;
  --accent-color3: #00f0e6;
  --text-light: #ffffff;
  --text-dark: #c0cad9;
  --text-error: #cf4c43;
}

h1{
  color: var(--text-light);
  font-size: 26px;
  text-align: center;
}

h2{

  color: var(--text-light);
  font-size: 20px;
  text-align: center;

}

p{
  color: var(--text-dark);
  font-size: 16px;
}

button{
  all: unset;
}

select {
  background-color: var(--secondary-color); 
  color: var(--text-dark); 
  border-radius: 10px 10px 0px 0px;
  border: solid 0;
  border-bottom-width: 2px;
  border-color: var(--text-dark);
  box-shadow: 0 7px 15px rgba(0, 0, 0, 0.619);
  margin-bottom: 40px;
  padding: 8px 12px; 
  font-size: 20px; 
  outline: none; 
  cursor: pointer; 
}
select:focus{
  outline: none;
  border: solid 0;
  border-bottom-width: 3px;
  border-color: var(--accent-color3);
}

option {
  background-color: var(--secondary-color); 
  color: #ffffff; 
}

.loading{
  position: fixed;
  display: flex;
  background-color: #022562;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

input {
  height: 50px;
  align-items: center;
  font-size: 20px;
  width: 100%;
  text-align: center;
  background-color: var(--secondary-color);
  color: var(--text-dark);
  border-radius: 10px 10px 0px 0px;
  border: solid 0;
  border-bottom-width: 2px;
  border-color: var(--text-dark);
  box-shadow: 0 7px 15px rgba(0, 0, 0, 0.619);
  margin-bottom: 40px;
}

input:focus{
  outline: none;
  border: solid 0;
  border-bottom-width: 3px;
  border-color: var(--accent-color3);
}

.input-error{
  border-bottom-width: 3px;
  border-color: var(--text-error);
}

.close-button{
  color: var(--accent-color2);
  width: 10px;
  height: 10px;
  transition: all 0.5s ease-in-out; 
}

.close-button:hover{
  transform: rotate(90deg);
}

.video{
  margin-top: 90px;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
  color: var(--text-light);
  padding: 20px;
  
}